import {
  JupyterFrontEnd,
  JupyterFrontEndPlugin,
  ILabShell
} from '@jupyterlab/application';

import { ICommandPalette } from '@jupyterlab/apputils';
import { INotebookTracker } from '@jupyterlab/notebook';

import { tokens, CoreExtension } from '@anaconda/shared';

import { AssistantPlugin } from '@anaconda/assistant_lib';

const assistantPlugin = new AssistantPlugin();

/**
 * Initialization data for the @anaconda/assistant extension.
 */
const plugin: JupyterFrontEndPlugin<void> = {
  id: '@anaconda/assistant:plugin',
  description: 'A JupyterLab extension.',
  autoStart: true,
  requires: [tokens.CoreToken, INotebookTracker, ICommandPalette],
  // LabShell is available only on jupyterlab
  optional: [ILabShell],
  activate: (
    app: JupyterFrontEnd,
    coreExtension: CoreExtension,
    notebookTracker: INotebookTracker,
    palette: ICommandPalette,
    labShell?: ILabShell
  ) => {
    const { corePlugin } = coreExtension;

    // Check if the app environment is Jupyter Notebook or JupyterLab
    const isJupyterNotebook = !labShell;

    assistantPlugin.activate(app, {
      corePlugin,
      notebookTracker,
      palette,
      isJupyterNotebook
    });
    console.log('JupyterLab extension @anaconda/assistant is activated!');
  }
};

export default plugin;
